.ButtonPanelNavigation{
    width:96%;
    display: block;
    background: #FFFFFF;
    padding:1% 2%;
}

.ButtonPanelNavigation button{
    border:1px solid #f2f2f2;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    background: #ffffff;
    color:#333333;
    font-size:16px;
    padding:5px 10px;
}

.ButtonPanelNavigation button.activeButons{
    background: #ff7300;
    color:#fff;
}

.AndroidCLASSCSS .PanelHeader__left{
    display:none;
}
.AndroidCLASSCSS .PanelHeader__right{
    display:none;
}

#dilivery{
    background: #FFFFFF;
}

.DescrProduct2{
    padding: 5px 0;
    padding-bottom:15px;
    font-size:12px;
}


.DescrProduct2:after{
    content: '';
    position: relative;
    width:100%;
    display: block;
    height:10px;
}

.imgPrewieProduct2 {
